<template>
  <div class="poster-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div  class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          ref="multipleTable"
          @selection-change="handleSelectionChange"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            label="菜单Logo"
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.pic+')'}" v-if="scope.row.pic"></i>
              </div>
            </template>
          </el-table-column>
		
        

          <el-table-column
            width="100"
            label="菜单名称"
            align="center"
            prop="name">
          </el-table-column>

          

          <el-table-column
            width="160"
            label="操作"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index)">编辑</el-button>
             
            </template>
          </el-table-column>

        </el-table>

      </div>
    </common-template>

    <!-- 添加、编辑 -->
    <el-dialog title="菜单图标设置" :visible.sync="editVisible">
      <el-form :model="menuLogo" ref="EditForm" :rules="rules" label-width="130px">
		<el-form-item label="菜单名称" prop="name">
          <el-input v-model.trim="menuLogo.name"></el-input>
        </el-form-item>
        <el-form-item label="图片" prop="pic">
          <ul class="app-image-list">
            <li class="image-item" v-if="menuLogo.pic" @click="showPhotoDialog=true">
              <img :src="menuLogo.pic" role="presentation" alt="" width="50">
            </li>
            <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--advert-card" style="width: 80px;height: 80px;line-height: 1;">
              <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
            </li>
          </ul>
        </el-form-item>
        </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="save()" :loading="loading" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <upload-photo
      :limit="1"
      action="change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>

  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import SiteAPI from '@/api/site.js'

  export default {
    name: 'menuLogos-list',
    components: { CommonTemplate, UploadPhoto },
    data() {
      return {
        showPhotoDialog: false,
        loading: false,
        editVisible: false,
        list: null,
        req:{},
        menuLogo: {},
        choose_id_arr: [],
        rules:{
          
        },
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        
        SiteAPI.getMenuList(this.req).then((res) => {
          this.list = res.list ? res.list : []
          
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      reload() {
        
        this.getList()
      },
     

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      handleEdit(idx){
        this.editVisible = true
        this.menuLogo = this.list[idx]
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.loading = true
            this.list[this.menuLogo.id] = this.menuLogo
            SiteAPI.saveMenu(JSON.stringify(this.list)).then((res) => {
              this.loading = false
              if (res.code === 0) {
                this.editVisible = false
                this.$message.success('保存成功！');
                this.reload()
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },
      //  图片添加
      confirmPhoto(pics) {
        this.menuLogo.pic = pics[0]
      },

    }
  }
</script>

<style lang="scss">

</style>



