<template>
  <div class="poster-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

       <div class="content__main-list" slot="list">
        <el-table
          ref="multipleTable"
          :data="[advert]"
          tooltip-effect="dark"
          style="width: 100%">
          <el-table-column
            label="广告信息"
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.pic+')'}" v-if="scope.row.pic"></i>
                <div class="item__info">
                  <p class="item__info-main" style="font-weight: 500;">{{scope.row.title}}</p>
                  <p class="item__info-sub">{{scope.row.linkman}}</p>
                  <p class="item__info-sub">{{scope.row.linkTel}}</p>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="广告跳转"
            align="left">
            <template slot-scope="scope">
              <div v-if="scope.row.type == 1">
                <p>跳转到小程序</p>
                <p>{{scope.row.appid}}</p>
                <p>{{scope.row.appPage}}</p>
              </div>
              <div v-if="scope.row.type == 2">
                <p>对接微信广告</p>
                <p>{{scope.row.wxadKey}}</p>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            width="160"
            label="是否显示"
            align="center"
            prop="createTime">
            <template slot-scope="scope">
              {{scope.row.frozen ? '已显示' : '隐藏中'}}
            </template>
          </el-table-column>
          <el-table-column
            width="160"
            label="创建时间"
            align="center"
            prop="createTime">
          </el-table-column>
          <el-table-column
            width="100"
            label="操作"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleAdvertEdit(scope.$index)">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <div class="content__main-operation" slot="header">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="$router.push('/energy/manager/2/add')">新增视频</el-button>
          <el-button size="small" plain @click="handleDelete(0)">批量删除</el-button>
        </div>
        <div class="operation-right">
          <el-select v-model="req.cid" size="small" placeholder="全部分类" @change="reload">
            <el-option :value="0" label="全部分类"></el-option>
            <el-option
              v-for="item,idx in cate_list"
              :key="idx"
              :label="item.name"
              :value="item.id"
              :style="{'padding-left': item.style }">
            </el-option>
          </el-select>
          <el-input size="small" v-model="req.keywords" placeholder="请输入内容" @keyup.enter.native="reload">
            <el-button slot="append" icon="el-icon-search" @click="reload()"></el-button>
          </el-input>
        </div>
      </div>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          ref="multipleTable"
          @selection-change="handleSelectionChange"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            label="视频信息"
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.thumb+')'}"></i>
                <div class="item__info">
                  <p class="item__info-main" style="font-weight: 500;">{{scope.row.title}} <el-tag type="danger" size="mini" v-if="scope.row.isHot==1">推荐</el-tag></p>
                  <p class="item__info-sub">栏目：{{scope.row.cname}}</p>
                  <p class="item__info-sub">作者：{{scope.row.author}}</p>
                  <p class="item__info-sub">分享可得能量：{{scope.row.shareEnergy}}</p>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            label="视频地址"
            align="left">
            <template slot-scope="scope">
              <a :href="scope.row.videoUrl" style="color: #409EFF;text-decoration: underline;" target="_blank">{{scope.row.videoUrl}}</a>
            </template>
          </el-table-column>

          <el-table-column
            width="150"
            label="发布状态"
            align="center"
            prop="createTime">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.frozen"
                :active-text="scope.row.frozen ? '已发布' : '隐藏中'"
                @change = "changeStatus(scope.row.id, scope.row.frozen)">
              </el-switch>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            label="发布时间"
            align="center"
            prop="createTime">
          </el-table-column>

          <el-table-column
            label="操作"
            width="160"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.row.id)">编辑</el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>

        </el-table>

        <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
          <el-pagination
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="page.pageSize"
            :current-page="page.pageNo"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.totalRows"
            @size-change = "changePageSize"
            @current-change = "changePageNo">
          </el-pagination>
        </div>

      </div>
    </common-template>

        <!-- 详情页广告 -->
    <el-dialog title="详情页广告" :visible.sync="editAdvertVisible">
      <el-form :model="advert" ref="AdvertForm" :rules="advertRules" label-width="130px">
        <el-form-item label="图片" prop="pic">
          <ul class="app-image-list">
            <li class="image-item" v-if="advert.pic" @click="showAdvertPicDialog=true">
              <img :src="advert.pic" role="presentation" alt="" width="50">
            </li>
            <li @click="showAdvertPicDialog=true" v-else class="el-upload el-upload--advert-card" style="width: 80px;height: 80px;line-height: 1;">
              <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
            </li>
          </ul>
        </el-form-item>
        <el-form-item label="客户名称" prop="linkman">
          <el-input v-model.trim="advert.linkman"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="linkTel">
          <el-input v-model.trim="advert.linkTel"></el-input>
        </el-form-item>
        <el-form-item label="广告标题" prop="title">
          <el-input v-model.trim="advert.title"></el-input>
          <p class="xs-assist-label">可以用于备注说明</p>
        </el-form-item>
        <el-form-item label="广告类型" prop="frozen">
          <el-radio-group v-model="advert.type">
            <el-radio :label="1"><span style="font-weight: normal">自营</span></el-radio>
            <el-radio :label="2"><span style="font-weight: normal">微信</span></el-radio>
          </el-radio-group>
        </el-form-item>
        <template v-if="advert.type == 1">
          <el-form-item label="小程序appid" prop="appid">
            <el-input v-model.trim="advert.appid"></el-input>
          </el-form-item>
          <el-form-item label="小程序页面" prop="appPage">
            <el-input v-model.trim="advert.appPage"></el-input>
          </el-form-item>
        </template>
        <template v-if="advert.type == 2">
          <el-form-item label="微信广告主key" prop="wxadKey">
            <el-input v-model.trim="advert.wxadKey"></el-input>
          </el-form-item>
        </template>
        <el-form-item label="是否显示" prop="frozen">
          <el-radio-group v-model="advert.frozen">
            <el-radio :label="1"><span style="font-weight: normal">显示</span></el-radio>
            <el-radio :label="0"><span style="font-weight: normal">隐藏</span></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editAdvertVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="saveAdvert()" :loading="btn_advert_loading" size="mini">确 定</el-button>
      </div>
    </el-dialog>

     <upload-photo
      :limit="1"
      action="change"
      :show.sync="showAdvertPicDialog"
      @confirm="confirmAdvertPic"
    ></upload-photo>
  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import PosterApi from '@/api/energy.poster.js'
  import AdvertApi from '@/api/advert.js'

  export default {
    name: 'poster-list',
    components: { CommonTemplate, UploadPhoto },
    data() {
    	  var checkAppid = (rule, value, callback) => {
        if (this.advert.type == 1 && !value) {
          callback(new Error('请填写小程序appid'));
        } else {
          callback();
        }
      };
      var checkWxadKey = (rule, value, callback) => {
        if (this.advert.type == 2 && !value) {
          callback(new Error('请填写微信广告主key'));
        } else {
          callback();
        }
      };
      return {
        list: null,
        req: {cid: 0, type: 2},
        page: {pageNo:1, pageSize: 20},
        choose_id_arr: [],
        cate_list: [],
        advert : { position: 3, sort: 1, frozen: true, type: 1, energy: 0},
        btn_advert_loading: false,
        showAdvertPicDialog: false,
        editAdvertVisible: false,
        advertRules:{
          linkman: [
            { required: true, message: '请填写客户名称', trigger: 'blur' },
          ],
          linkTel: [
            { required: true, message: '请填写客户联系方式', trigger: 'blur' },
          ],
          title: [
            { required: true, message: '请填写广告标题', trigger: 'blur' },
          ],
          appid: [
            { validator: checkAppid, trigger: 'blur' },
          ],
          wxadKey: [
            { validator: checkWxadKey, trigger: 'blur' },
          ],
        }
      }
    },
    created() {
      this.getList()
      this.getCateList()
      this.getAdvert()
    },
    methods: {
      getList() {
        this.req.p = this.page.pageNo
        this.req.s = this.page.pageSize
        PosterApi.getList(this.req).then((res) => {
          this.list = res.list
          this.list.forEach(item => {
            item.frozen = Boolean(item.frozen)
          })
          this.page = res.page
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      reload() {
        this.page.pageNo = 1
        this.getList()
      },
      changePageSize(value){ // 修改每页条数size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getList()
      },
      changePageNo(value){ // 修改当前页currentPage
        this.page.pageNo = value
        this.list = null
        this.getList()
      },

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      // 编辑
      handleEdit(id) {
        this.$router.push({path:`/energy/manager/2/edit/${id}`})
      },

      // 上下架
      changeStatus(id, flag) {
        PosterApi.batchPublish([id], !flag?0:1).then((res) => {
          this.$message.success('操作成功');
          this.reload()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      // 批量删除
      handleDelete(id) {
        let ids = [id]
        if (id == 0) { // 批量删除
          if (this.choose_id_arr.length === 0) {
            this.$message.error("请至少选择一项！");
            return
          }
          ids = this.choose_id_arr.map(item => item.id)
        }

        this.$confirm('删除后无法恢复，是否确认？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          PosterApi.batchDelete(ids).then((res) => {
            this.$message.success('操作成功');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        })
      },

      // 分类选择列表
      getCateList() {
        PosterApi.getCategoryChooseList(2).then((res) => {
          this.getChooseList(res.list, 0)
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      getChooseList(list, hr){
        if (!list){
          return
        }
        if (hr == 0) this.cate_list=[]
        hr += 1
        list.forEach(item => {
          var tip = 20
          for (var i=2; i<=hr; i++){
            tip += 10
          }
          this.cate_list.push({
            id: item.id,
            name: item.name,
            style: tip + 'px'
          })
          this.getChooseList(item.list, hr)
        })
      },

     getAdvert() {
     	console.log('getadvert')
        PosterApi.getAdvert(8).then((res) => {
          this.advert = res.advert
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      saveAdvert() {
        this.$refs.AdvertForm.validate((valid) => {
          if (valid) {
            this.btn_advert_loading = true
            AdvertApi.save(this.advert).then((res) => {
              this.btn_advert_loading = false
              if (res.code === 0) {
                this.editAdvertVisible = false
                this.$message.success('保存成功！');
                this.reload()
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.btn_advert_loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },
      handleAdvertEdit() {
        this.editAdvertVisible = true
      },
      //  图片添加
      confirmAdvertPic(pics) {
        this.advert.pic = pics[0]
      },
    }

  }
</script>

<style lang="scss">

</style>



