<template>
  <div class="poster-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div class="content__main-operation" slot="header">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="handleAdd">新增语录</el-button>
          <el-button size="small" plain @click="handleDelete(0)">批量删除</el-button>
        </div>
      </div>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          ref="multipleTable"
          @selection-change="handleSelectionChange"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            label="发布者"
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.photo+')'}" v-if="scope.row.photo"></i>
                <div class="item__info">
                  <p class="item__info-main" style="font-weight: 500;" >{{scope.row.username}} <el-tag size="mini" :type="scope.row.memberId?'warning':'danger'">{{scope.row.memberId?"微信用户":"平台"}}</el-tag></p>
                  <p class="item__info-sub"></p>
                </div>
              </div>
            </template>
          </el-table-column>

          <el-table-column
            label="语录内容"
            align="center"
            prop="content">
          </el-table-column>

          <el-table-column
            label="点赞数"
            align="center"
            prop="zanNum">
          </el-table-column>

          <el-table-column
            width="80"
            label="排序"
            align="center"
            prop="sort">
          </el-table-column>

          <el-table-column
            width="160"
            label="发布状态"
            align="center">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.frozen"
                :active-text="scope.row.frozen ? '已发布' : '隐藏中'"
                @change = "changeStatus(scope.row.id, scope.row.frozen)">
              </el-switch>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            label="创建时间"
            align="center"
            prop="createTime">
          </el-table-column>

          <el-table-column
            width="150"
            label="操作"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index)">编辑</el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>

        </el-table>

      </div>
    </common-template>

    <!-- 添加、编辑 -->
    <el-dialog title="励志语录" :visible.sync="editVisible">
      <el-form :model="quotes" ref="EditForm" :rules="rules" label-width="130px">

        <el-form-item label="头像" prop="photo">
          <ul class="app-image-list">
            <li class="image-item" v-if="quotes.photo" @click="showPhotoDialog=true">
              <img :src="quotes.photo" role="presentation" alt="" width="50">
            </li>
            <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--quotes-card" style="width: 80px;height: 80px;line-height: 1;">
              <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
            </li>
          </ul>
        </el-form-item>

        <el-form-item label="署名" prop="username">
          <el-input v-model.trim="quotes.username"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input type="textarea" rows="5" v-model.trim="quotes.content"></el-input>
        </el-form-item>
        <el-form-item label="点赞数" prop="zanNum">
          <el-input v-model.trim="quotes.zanNum"></el-input>
        </el-form-item>

        <el-form-item label="排序字段" prop="sort">
          <el-input size="small" v-model="quotes.sort"></el-input>
          <p class="xs-assist-label">按数值大小升序，越小排在越前</p>
        </el-form-item>

        <el-form-item label="显示状态" prop="frozen">
          <el-switch v-model="quotes.frozen" :active-text="quotes.frozen ? '显示中' : '已冻结'"></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="save()" :loading="loading" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <upload-photo
      :limit="1"
      action="change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>

  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import quotesApi from '@/api/quotes.js'

  export default {
    name: 'quotes-list',
    components: { CommonTemplate, UploadPhoto },
    data() {
      return {
        showPhotoDialog: false,
        loading: false,
        editVisible: false,
        list: null,
        quotes: {sort: 100, frozen: true},
        req: {},
        choose_id_arr: [],
        rules:{
          photo: [
            { required: true, message: '请上传头像', trigger: 'blur' },
          ],
          content: [
            { required: true, message: '请填写语录内容', trigger: 'blur' },
          ],
          username: [
            { required: true, message: '请填写署名', trigger: 'blur' },
          ],
        }
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        quotesApi.getList(this.req).then((res) => {
          this.list = res.list ? res.list : []
          this.list.forEach(item => {
            item.frozen = Boolean(item.frozen)
          })
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      reload() {
        this.getList()
      },

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      handleAdd(){
        this.editVisible = true
        this.quotes = { sort: 100, frozen: true}
      },
      handleEdit(idx){
        this.editVisible = true
        this.quotes = this.list[idx]
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.loading = true
            quotesApi.save(this.quotes).then((res) => {
              this.loading = false
              if (res.code === 0) {
                this.editVisible = false
                this.$message.success('保存成功！');
                this.reload()
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },

      // 上下架
      changeStatus(id, flag) {
        quotesApi.batchPublish([id], !flag?0:1).then((res) => {
          this.$message.success('操作成功');
          this.reload()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      // 批量删除
      handleDelete(id) {
        let ids = [id]
        if (id == 0) { // 批量删除
          if (this.choose_id_arr.length === 0) {
            this.$message.error("请至少选择一项！");
            return
          }
          ids = this.choose_id_arr.map(item => item.id)
        }

        this.$confirm('删除后无法恢复，是否确认？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          quotesApi.batchDelete(ids).then((res) => {
            this.$message.success('操作成功');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        })
      },

      //  图片添加
      confirmPhoto(pics) {
        this.quotes.photo = pics[0]
      },

    }
  }
</script>

<style lang="scss">

</style>



