<template>
  <div class="common-add-content goods-add-content">
    <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

    <div class="content__main" style="margin-top: 20px;">
      <el-tabs v-model="activeName">
        <el-tab-pane label="站点信息" class="main-first" name="first" style="padding-bottom: 30px;">
          <div class="section">
            <el-form :rules="rules" ref="EditForm" :model="site" :label-width="formLabelWidth" style="width: 600px;">
              <el-form-item label="LOGO">
                <ul class="app-image-list">
                  <li class="image-item" v-if="site.logo" @click="showPhotoDialog=true">
                    <img :src="site.logo" role="presentation" alt="" width="80">
                  </li>
                  <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--picture-card" style="width: 80px;height: 80px;line-height: 1;">
                    <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
                  </li>
                </ul>
              </el-form-item>
              <el-form-item label="名称" prop="name">
                <el-input size="small" v-model="site.name"></el-input>
              </el-form-item>
              </el-form-item>
              <el-form-item label="线上版本" prop="version">
                <el-input size="small" v-model="site.version"></el-input>
              </el-form-item>

              <el-form-item label="联系电话" prop="mobile">
                <el-input size="small" v-model="site.mobile"></el-input>
              </el-form-item>
              <el-form-item label="联系邮箱" prop="email">
                <el-input size="small" v-model="site.email"></el-input>
              </el-form-item>
              <el-form-item label="工作时间" prop="workTime">
                <el-input size="small" v-model="site.workTime"></el-input>
              </el-form-item>

              <el-form-item label="推荐语" prop="intro" >
                <el-input type="textarea" :rows="2" size="small" v-model="site.intro"></el-input>
              </el-form-item>
              <el-form-item label="简介" prop="remark" >
                <el-input type="textarea" :rows="5" size="small" v-model="site.remark"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <div class="next-footer" style="margin-left:120px;">
            <el-button size="small" type="primary" @click="save" :loading="btn_loading">保存并下一步</el-button>
          </div>
        </el-tab-pane>

        <el-tab-pane label="分享设置" name="second" style="padding-bottom: 30px;">
          <el-form :model="site" :label-width="formLabelWidth">
            <el-form-item label="分享描述" prop="shareTitle" >
              <el-input type="textarea" :rows="2" size="small" v-model="site.shareTitle"></el-input>
            </el-form-item>
            <el-form-item label="分享路径" prop="sharePath" >
              <el-input  size="small" v-model="site.sharePath"></el-input>
            </el-form-item>
            <el-form-item label="分享图片" prop="sharePic">
              <ul class="app-image-list">
                <li class="image-item" v-if="site.sharePic" @click="showPicDialog=true">
                  <img :src="site.sharePic" role="presentation" alt="" width="100">
                </li>
                <li @click="showPicDialog=true" v-else class="el-upload el-upload--picture-card" style="width: 100px;height: 100px;line-height: 1;">
                  <i class="el-icon-plus" style="font-size: 50px;margin-top: 25px;"></i>
                </li>
              </ul>
            </el-form-item>
          </el-form>
          <div class="next-footer" style="margin-left:120px;margin-top: 10px;">
            <el-button size="small" type="primary" @click="saveShareInfo()" :loading="btn_share_loading">保存</el-button>
            <el-button size="small" @click="activeName='first'">上一步</el-button>
          </div>
        </el-tab-pane>

      </el-tabs>
    </div>

    <!-- 上传图片 -->
    <upload-photo
      :limit="1"
      action = "change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>
    <!-- 上传图片 -->
    <upload-photo
      :limit="1"
      action = "change"
      :show.sync="showPicDialog"
      @confirm="confirmPic"
    ></upload-photo>

  </div>

</template>

<script>
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import SiteApi from '@/api/site.js';

  export default {
    name: 'site-list',
    components: {
      UploadPhoto
    },
    data() {
      return {
        showPhotoDialog: false,
        showPicDialog: false,

        btn_loading: false,
        btn_share_loading: false,

        activeName: "first",
        formLabelWidth: '120px',
        site: {},
        rules:{
          name: [
            { required: true, message: '请输入名称', trigger: 'blur' },
          ],
          logo: [
            { required: true, message: '请上传logo', trigger: 'blur' },
          ],
        },
      }
    },
    created() {
      this.getSiteInfo()
    },
    methods: {

      getSiteInfo() {
        SiteApi.info().then((res) => {
          this.site = res.site
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.btn_loading = true
            SiteApi.save(this.site).then((res) => {
              this.btn_loading = false
              if (res.code === 0) {
                this.$message.success('保存成功！');
                this.site.id = res.siteId
                this.activeName = 'second'
                this.getSiteInfo()
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.btn_loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },
      saveShareInfo() {
        this.btn_share_loading = true
        SiteApi.save(this.site).then((res) => {
          this.btn_share_loading = false
          if (res.code === 0) {
            this.$message.success('保存成功！');
            this.activeName = 'first'
          } else {
            this.$message.error(res.msg);
          }
        }, (err) => {
          this.btn_share_loading = false
          this.$message.error(err.msg);
        })
      },
      // 确认图片
      confirmPhoto(pics) {
        this.site.logo = pics[0]
      },
      // 确认图片
      confirmPic(pics) {
        this.site.sharePic = pics[0]
      },
    }
  }
</script>

<style lang="scss">
  .goods-add-content {
    .form__button {
      font-size: 12px;
      color: #38f;
      margin-left: 5px;
    }
  }
</style>



