
import http from "@/utils/http";


export default {

  /**
   * 列表
   */
  getList : params => {
    return http.post("/orders/list", params)
  },

  /**
   * 备注
   */
  remarks : (ordersNo, value) => {
    return http.post("/orders/" + ordersNo + "/remarks/", {value: value})
  },

  /**
   * 取消订单
   */
  cancel : ordersNo => {
    return http.post("/orders/" + ordersNo + "/cancel", {})
  },

  /**
   * 确认发货
   */
  delivery : (ordersNo, param) => {
    return http.post("/orders/" + ordersNo + "/delivery", param)
  },

}



