<template>
  <div class="order-content">
    <h1 class="content__title">{{$route.meta.title}}</h1>
    <div class="content__main">
      <div class="content__main-operation">
        <div class="operation-top">

          <div class="operation-top__l">
            <div class="operation-item">
              <span>下单时间: </span>
              <el-date-picker
                v-model="ordersReq.timeRange"
                type="daterange"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOption"
                format="yyyy-MM-dd"
                @change = "changeTimeRange"
                size="mini"
                class="xs-date-picker">
              </el-date-picker>
            </div>
            <div class="operation-item">
              <span>订单类型: </span>
              <el-select v-model="ordersReq.type" @change="changeOrderType" size="mini" placeholder="请选择订单类型">
                <el-option
                  v-for="item,idx in typeList"
                  :key="item.idx"
                  :label="item"
                  :value="idx">
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="operation-item">
            <el-input placeholder="请输入内容" v-model="ordersReq.inputKeywords" class="input-with-select" size="mini" @keyup.enter.native="reload">
              <el-select v-model="ordersReq.inputType" slot="prepend" placeholder="订单编号" style="width: 100px;padding-right: 10px; padding-left: 5px;">
                <el-option v-for="item,idx in inputTypeList" :key="idx" :label="item" :value="idx"></el-option>
              </el-select>
              <el-button slot="append" icon="el-icon-search" @click="reload()"></el-button>
            </el-input>
          </div>
        </div>
        <div class="operation-bottom">
          <div class="operation-item">
            <span>订单状态: </span>
              <el-badge :value="item.status!=1&&item.status!=2?0:(item.status===1?number1:number2)" class="item" v-for="item,idx in statusList"
                      :key="idx" :hidden="(item.status!=1&&item.status!=2) || (item.status===1&&number1===0) || (item.status===2&&number2===0)" >
                <el-button :type="ordersReq.status === item.status && ordersReq.groupStatus==item.groupStatus  ? 'primary' : ''"
                           size="mini"
                           @click="ordersReq.status=item.status;ordersReq.groupStatus=item.groupStatus;reload()">
                  {{item.name}}
                </el-button>
              </el-badge>
          </div>
        </div>
      </div>
      <div class="content__main-list">
        <div class="order-table" v-loading="!list">
          <div class="table-header">
            <span class="main">商品信息</span>
            <span class="price">兑换能量</span>
            <span class="fixed">配送信息</span>
            <span class="fixed">订单状态</span>
          </div>

          <ul class="table-body" >
            <li class="order-item" v-for="item,idx in list" :key="idx">
              <div class="item__header">
                <span>订单编号: {{item.ordersNo}}  <el-tag :type="tag_types[item.type]" size="mini" :hit="true" :disable-transitions="true">{{typeList[item.type]}}</el-tag></span>
                <span>下单时间: {{item.createTime}}</span>
                <span v-if="item.groupOrderId>0">团编号:{{item.groupOrderId}}</span>
                <el-button type="text" size="mini" @click="remarks(idx)">备注</el-button>
              </div>
              <div class="item__content" >
                <div class="main">
                  <div class="content-info_item" v-for="item2,idx2 in item.itemList" :key="idx2">
                    <i :style="{'backgroundImage': 'url('+item2.pic+')'}"></i>
                    <div>
                      <div class="item__name">
                        <h3>{{item2.name}}</h3>
                        <template v-if="item.status != 1 && item.status != 6">
                          <el-tag size="mini" v-if="item2.status === 2">已发</el-tag>
                          <el-tag size="mini" type="danger" v-if="!item2.status || item2.status === 1">待发</el-tag>
                        </template>
                      </div>
                      <div>
                        <span>数量: {{item2.number}}</span>
                        <span v-if="item2.attrsStr">【{{item2.attrsStr}}】</span>
                      </div>
                      <div>
                        <span>能量: {{item2.energy}} <template v-if="item2.price">+ ￥{{item2.price}}</template></span>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="item__pay price">
                  <div>
                    <p>{{item.energy}}能量 <template v-if="item.price"> + ￥{{item.price}}</template></p>
                  </div>
                </div>
                <div class="item__concat">
                  <p>收货人: {{item.username}}</p>
                  <p>收货地址: {{item.address}}</p>
                  <p>电话: {{item.phone}}</p>
                  <template v-if="item.deliveryInfo">
                    <p>快递公司: {{item.deliveryInfo.company}}</p>
                    <p>快递单号: {{item.deliveryInfo.number}}</p>
                  </template>
                </div>
                <div class="item__status">
                  <!-- 订单状态 -->
                  <p>{{order_status[item.status]}}</p>
                  <!-- 操作按钮 -->
                  <el-button v-if="item.status === 2" size="mini" type="primary" plain @click="cancelOrders(idx)">取消</el-button>
                  <el-button v-if="item.status === 2" size="mini" type="primary" plain @click="showDeliveryPop(idx)">发货</el-button>
                </div>
              </div>
              <div class="item__header " v-if="item.message" style="background: #ffebc1;color: #dfa700;border-bottom:none;border-top: #f2f2f6 solid 1px;">
                买家留言：{{item.message}}
              </div>
              <div class="item__header" v-if="item.remark" style="background: #fbd0cd;color: #fb7168;border-bottom:none;border-top: #f2f2f6 solid 1px;">
                卖家备注：{{item.remark}}
              </div>
            </li>
          </ul>
        </div>
      </div>

      <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
        <el-pagination
          :page-sizes="[10, 20, 30, 40, 50]"
          :page-size="page.pageSize"
          :current-page="page.pageNo"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.totalRows"
          @size-change="changePageSize"
          @current-change = "changePageNo">
        </el-pagination>
      </div>

      <!-- 发货 -->
      <el-dialog title="商品发货" class="xs-dialog deliveryPop" :visible.sync="showDelivery">
        <el-tabs @tab-click="chooseDeliveryItem" v-model="deliveryReq.index">
          <el-tab-pane label="未发货" name="0" v-if="todolist && todolist.length>0"></el-tab-pane>
          <el-tab-pane v-for="item,idx in delivery_list" :label="'包裹' + (idx+1)" :name="(idx+1) + ''" :key="idx"></el-tab-pane>
        </el-tabs>

        <div class="order-table" style="border: 1px solid #efefef;">
          <div class="table-header">
          <span class="cell" v-if="deliveryReq.id == null">
              <el-checkbox :checked="checkAllFlag" v-model="checkAllFlag" @change="checkedAll"></el-checkbox>
            </span>
            <span class="main">商品信息</span>
            <span class="fixed">数量</span>
          </div>

          <ul class="table-body">
            <li class="order-item">
              <div class="item__content" v-for="item2,idx2 in deliveryReq.itemList" :key="idx2">
                <div class="cell" v-if="deliveryReq.id == null">
                  <el-checkbox :label="item2.id" v-model="goods_checked_arr" @change="checkedItem">&nbsp;&nbsp;</el-checkbox>
                </div>
                <div class="main">
                  <div class="content-info_item">
                    <i :style="{'backgroundImage': 'url('+item2.pic+')'}"></i>
                    <div>
                      <div class="item__name">
                        <h3>{{item2.name}}</h3>
                        <el-tag size="mini" v-if="item2.status === 2">已发</el-tag>
                        <el-tag size="mini" type="danger" v-if="!item2.status || item2.status === 1">待发</el-tag>
                      </div>
                      <div>
                        <span v-if="item2.attrsStr">【{{item2.attrsStr}}】</span>
                      </div>
                      <div>
                        <span>能量: ¥{{item2.amount}}</span>
                        <span>x {{item2.number}}</span>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="item__status">
                  <span>{{item2.number}}</span>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <el-form>
          <el-form-item label="订单编号">
            <span>{{orders.ordersNo}}</span>
          </el-form-item>
          <el-form-item label="下单时间">
            <span>{{orders.createTime}}</span>
          </el-form-item>
          <el-form-item label="收货信息">
            <span>{{orders.username}}, {{orders.phone}}, {{orders.address}}</span>
          </el-form-item>
          <el-form-item label="发货方式">
            <div>
              <el-radio :label="1" :disabled="deliveryReq.id!=null?true:false" v-model="deliveryReq.deliveryType">物流信息</el-radio>
              <el-radio :label="2" :disabled="deliveryReq.id!=null?true:false" v-model="deliveryReq.deliveryType">无需物流</el-radio>
            </div>
          </el-form-item>

          <template v-if="deliveryReq.id === 0">
            <el-form-item label="快递公司">
              <span>{{deliveryReq.deliveryWay}}</span>
            </el-form-item>
            <el-form-item label="快递单号">
              <span>{{deliveryReq.deliveryNo}}</span>
            </el-form-item>
          </template>

          <template v-else-if="deliveryReq.deliveryType === 1">
            <el-form-item label="快递公司">
              <el-autocomplete
                v-model="deliveryReq.deliveryWay"
                :fetch-suggestions="querySearch"
                placeholder="请输入内容"
                size="small"
              ></el-autocomplete>
            </el-form-item>
            <el-form-item label="快递单号" >
              <el-input size="small" style="width: 300px;" v-model="deliveryReq.deliveryNo" ></el-input>
            </el-form-item>
          </template>

        </el-form>
        <div slot="footer" class="dialog-footer" v-if="orders.status < 4 || orders.status==70">
          <el-button @click="closeDeliveryPop" size="small">{{deliveryReq.id!=0?"取 消":"关 闭"}}</el-button>
          <el-button v-if="deliveryReq.id != 0" type="primary" @click="confirmDelivery" size="small">确 定</el-button>
        </div>
      </el-dialog>

    </div>
  </div>

</template>

<script>

  import OrdersApi from 'api/store.orders.js'

  export default {
    name: 'orders-list',
    components: { },
    data () {
      return {
        list: null,

        dialogFormVisible: true,
        order_status: ["全部","","待发货","已发货，待收货","已完成","已评价","交易关闭","已退款"],

        typeList: ["全部", "兑换商品", "一分兑换"],
        tag_types: ["", "info", "warning"],

        status_arr: [
          {name: "全部", status: null},
          {name: "待发货", status: 2},
          {name: "已发货", status: 3},
          {name: "已完成", status: 4},
          {name: "已关闭", status: 6},
//          {name: "已退款", status: 7},
        ],
        number1: 0, // 待付款订单数量
        number2: 0, // 待发货订单数量
        statusList: [],
        inputTypeList: ["订单号", "收货人", "联系电话"],
        ordersReq: {type: 0, status: null, timeRange: '', inputType: 0, inputKeywords:""},
        page: {pageNo: 1, pageSize: 10},
        pickerOption: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        companys: [{"value":"顺丰快递"},{"value":"申通快递"},{"value":"圆通快递"},{"value":"中通快递"},{"value":"百世快递"},{"value":"韵达快递"},{"value":"天天快递"},{"value":"中国邮政"},{"value":"邮政EMS"},{"value":"宅急送"},{"value":"德邦快递"},{"value":"全峰快递"},{"value":"其他快递"}],
        orders: {},

        checkAllFlag : false,// 全选标记
        goods_checked_arr: [],
        delivery_list: [],
        deliveryReq: {type: 1, index: 0,},
        showDelivery: false, // 发货弹窗
        todolist: [], // 待发货列表
      }
    },
    created(){
      this.statusList = this.status_arr
      this.getOrdersList()
//      this.getOrdersCount()
    },
    methods: {
      // 取消订单
      cancelOrders(idx) {
        this.$confirm('是否确认取消并关闭订单?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          OrdersApi.cancel(this.list[idx].ordersNo).then((res) => {
            this.$message.success('操作成功');
            this.list.splice(idx, 1)
          }, (err) => {
            this.$message.error(err.msg);
          })
        }).catch(() => {
        });
      },
      // 备注
      remarks(idx) {
        this.$prompt('请输入备注内容', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /.+/,
          inputErrorMessage: '备注内容不能为空',
        }).then(({ value }) => {
          OrdersApi.remarks(this.list[idx].ordersNo, value).then((res) => {
            this.list[idx].remark = value
          }, (err) => {
            this.$message.error(err.msg);
          })
        }).catch(() => {
        });
      },
      // 发货
      querySearch(queryString, cb) {
        var companys = this.companys;
        var results = queryString ? companys.filter(this.createFilter(queryString)) : companys;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (company) => {
          return (company.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      chooseDeliveryItem() {
        var list = []
        if (this.deliveryReq.index === '0') { // 待发货
          list = this.todolist
        } else {
          this.deliveryReq = this.delivery_list[this.deliveryReq.index-1]
          this.deliveryReq.itemList = this.orders.itemList.filter(item => item.deliveryId==this.deliveryReq.id)
        }
        this.deliveryReq.list = list
      },
      showDeliveryPop(idx) {
        this.orders = this.list[idx]
        this.orders.idx = idx
        this.showDelivery = true
        this.goods_checked_arr = []
        // 加载发货清单
        var todolist = [], delivery_list = []
        if (this.orders.status < 4) {
          this.orders.itemList.forEach(item => {
            if (!item.status || item.status === 1) {
              todolist.push(item)
            } else {
              delivery_list.push(item.deliveryInfo)
            }
          })
        }
        this.todolist = todolist;
        this.delivery_list = delivery_list;

        if (this.todolist && this.todolist.length > 0) {
          this.deliveryReq = {
            index: '0',
            deliveryType: 1,
            ordersNo: this.orders.ordersNo,
            ordersId: this.orders.id,
            itemList: this.todolist
          }
        }else {
          this.deliveryReq = this.delivery_list[0]
        }

        // 默认全选
        this.checkAllFlag = true
        this.checkedAll()
      },
      closeDeliveryPop() {
        this.todolist = []
        this.delivery_list = []
        this.deliveryReq = {
          deliveryType: 1,
          index: '0',
        }
        this.showDelivery = false
      },
      confirmDelivery() { // 确认发货
        var orderItems = []
        if (this.deliveryReq.id) {
          orderItems = this.deliveryReq.itemList.map(item => item.id)
        }else {
          orderItems = this.goods_checked_arr
        }
        if (orderItems.length === 0) {
          this.$message.error("请至少选择一个商品进行发货！")
          return
        }
        if (this.deliveryReq.deliveryType === 1){ // 发货方式为物流信息时必填
          if (!this.deliveryReq.deliveryWay) {
            this.$message.error("请选择物流公司！")
            return
          }
          if (!this.deliveryReq.deliveryNo) {
            this.$message.error("请完善物流单号！")
            return
          }
        }
        var ordersDelivery = {
          id: this.deliveryReq.id,
          deliveryType: this.deliveryReq.deliveryType,
          deliveryWay: this.deliveryReq.deliveryWay,
          deliveryNo: this.deliveryReq.deliveryNo,
          orderItems: orderItems,
        }
        this.$confirm('是否确认发货?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          OrdersApi.delivery(this.deliveryReq.ordersNo, ordersDelivery).then((res) => {
            loading.close()
            if (res.code === 0) {
              this.closeDeliveryPop()
              this.reload()
              if (res.errorCode && res.errorCode === 600) {
                this.$message.error(res.msg);
              }else {
                this.$message.success('操作成功');
              }
            }else {
              this.$message.error(res.msg);
            }
          }, (err) => {
            loading.close()
            this.$message.error(err.msg);
          })
        }).catch(() => {
        });
      },

      // 订单列表
      getOrdersList() {
        this.ordersReq.p = this.page.pageNo
        this.ordersReq.s = this.page.pageSize
        if(this.ordersReq.inputType === 0) {
          this.ordersReq.ordersNo = this.ordersReq.inputKeywords
          this.ordersReq.username = null
          this.ordersReq.phone = null
        }else if(this.ordersReq.inputType === 1) {
          this.ordersReq.orderNo = null
          this.ordersReq.username = this.ordersReq.inputKeywords
          this.ordersReq.phone = null
        }else if(this.ordersReq.inputType === 2) {
          this.ordersReq.orderNo = null
          this.ordersReq.username = null
          this.ordersReq.phone = this.ordersReq.inputKeywords
        }
        OrdersApi.getList(this.ordersReq).then((res) => {
          console.log(res)
          this.page = res.page
          this.list = res.list ? res.list : []
          this.list.forEach(item => {
            item.itemList.forEach(item2 => {
              if (item2.attrs) {
                item2.attrsStr = JSON.parse(item2.attrs).join(" & ")
              }
            })
          })
        }, (err) => {
          this.list = []
          this.$message.error(err.msg);
        })
      },


      // 全选、反选
      checkedAll() {
        if (!this.checkAllFlag) {//实现反选
          this.goods_checked_arr = [];
        }else{ //实现全选
          this.goods_checked_arr = [];
          this.deliveryReq.itemList.forEach(item => {
            this.goods_checked_arr.push(item.id)
          });
        }
      },
      checkedItem(value) {
        if (!value){
          this.checkAllFlag = false
        }else {
          this.checkAllFlag = this.goods_checked_arr.length === this.deliveryReq.itemList.length
        }
      },
      // 切换订单状态
      changeOrderType() {
        this.ordersReq.status = null
        this.reload()
      },
      changeTimeRange() { // 修改时间
        if (this.ordersReq.timeRange) {
          this.ordersReq.start = this.ordersReq.timeRange[0]
          this.ordersReq.end = this.ordersReq.timeRange[1]
        } else {
          this.ordersReq.start = null
          this.ordersReq.end = null
        }
        this.reload()
      },

      reload(){
        this.list = null
        this.page.pageNo = 1
        this.getOrdersList()
      },
      changePageSize(value){ // 修改每页条数size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getOrdersList()
      },
      changePageNo(value){ // 修改当前页currentPage
        this.page.pageNo = value
        this.list = null
        this.getOrdersList()
      },

    }

  }
</script>


