          }

          .xs-date-picker {
            width: 220px;

            flex:0 0 auto;
          }


          .el-select .el-input {
            width: 110px;
          }
          .input-with-select .el-input-group__prepend {
            background-color: #fff;
          }

          .el-input-group__append {
            padding: 0 10px;
          }
        }

        .operation-top {
          @include flex($al: center);
          @include flex-wrap();

          .operation-top__l{
            @include flex($al: center);
            @include flex-full(1 1 auto);
            @include flex-wrap();
          }
          .operation-item {
            margin-bottom: $common_space_v;

          }
        }

        .operation-bottom {
          margin-bottom: $common_space_v;
          @include flex($al: center);

          .el-badge {
            margin-right: 20px;
          }
        }
      }

      .content__main-list {


      }
    }
  }
  $order_item_fixed_width: 40%;
  .order-table {
    border-radius: 6px;
    overflow: hidden;


    .table-header {
      background: #fff;
      @include flex();

      > span {
        @include flex-full();
        font-size: 12px;
        font-weight: 500;
        color: #333;
        padding: 16px 15px;
        display: block;
        border-bottom: 1px solid #ededef;

        &.main {
          width: $order_item_fixed_width;
          @include flex-fixed();
        }
        &.fixed {
           text-align: center;
        }
        &.price {
           width:120px;
           flex:0 0 auto;
           text-align: center;
           @include box-sizing();
        }
        &.cell {
          width:50px;
          flex:0 0 auto;
          @include box-sizing();
        }

      }
    }

    .table-body {
      .order-item {
        background: #fff;

        margin-top: 10px;

        .item__header {
          padding: 10px 15px;
          font-size: 12px;
          color: #666;
          border-bottom: #f2f2f6 solid 1px;

          span {
            margin-right: 20px;
          }

        }

        .item__content {
          @include flex($al: center);

          > div {

            line-height: 1.3;

            @include flex-full();
            width:0;
            padding: 16px 15px;
            font-size: 12px;
            position: relative;
            text-align: center;
            &.main {
              width: $order_item_fixed_width;
              @include flex-fixed;
              text-align: left;
            }
            &.price {
               width:120px;
               flex:0 0 auto;
               @include box-sizing();
            }
            &.cell {
              width:50px;
              flex:0 0 auto;
              @include box-sizing();
            }

            p {
              font-size: 12px;
            }

          }

          .item__pay {

            p {
              span {
                color: #999;
              }
            }

            &:hover {
              .xs-link-button {
                display: block;
              }
            }

            .xs-link-button {
              color: #38f;
              display: none;
              left: 50%;
              transform: translateX(-50%);
              position: absolute;
              top:46px;
            }
          }


          .item__status {
            line-height: 2;
          }

          .content-info_item {
            @include flex($al: center);
            margin-bottom: 10px;

            .item__name {
              @include flex;
              h3 {
                margin-right: 10px;
              }
            }

            &:last-child {
              margin-bottom: 0;
            }

            > i {
              width:60px;
              height:60px;
              @include flex-fixed;
              background-size: contain;
            }

            > div {
              height:60px;
              margin-left: 10px;
              @include flex(column);
              h3 {
                margin-top: 4px;
                font-size: 13px;
                font-weight: bold;
                color: #333;
                margin-bottom: 10px;
                overflow: hidden;
                text-overflow: ellipsis;
                display: box;
                display: -webkit-box;
                line-height: 1;
                line-clamp: 1;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                height:13px;
              }

              div {
                color: #999;
                font-size: 12px;
                @include flex-full();

                span {
                  margin-right: 10px;
                }
              }
            }
          }
        }
      }
    }
  }

</style>




