
import http from '@/utils/http'


export default {

  info : () => {
      return http.post("/site/info", {})
  },

  // 信息
  save : param => {
      return http.post("/site/save", param)
  },
  
  // 列表
  getMenuList : params => {
    return http.post("/site/menu/list", {})
  },

  // 保存
  saveMenu : params => {
    return http.post("/site/menu/save", params)
  },

}



